* {
  box-sizing: border-box;
  /* color: var(--clr-font); */
}

:root{
    --default-clr: rgb(170,80,80);
    --clr-font: rgb(10,25,10);
    --clr-primary: rgb(210,210,210);
    --clr-secondary: rgb(90,90,90);
  }

.body-chacha {
  width: 100vw;
  height: 100vh;
  background-color: var(--default-clr);
}

#quote-box {
  margin: auto;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  height: auto;
  padding: 40px 50px;
  width: 450px;
  background-color: var(--clr-primary);;
  border-radius: 5px;
}

.quote-text {
  text-align: center;
  font-size: 1.75em;
  height: auto;
  display: inline;
}

#text {
	position: relative;
	text-align: center;
  color: var(--default-clr);
}

#quoteLeft{
  color: var(--default-clr);
}

.quote-author{
	padding: 25px 0px 15px 0px;
	text-align: right;
  color: var(--clr-secondary);
}

#author {
	font-size: 1em;
	color: var(--clr-font-secondary);
}

#new-quote{ /* button*/
	float: right;
	border: none;
	margin-top: 10px;
	padding: 8px 18px 8px 18px;
	cursor: pointer;
	border-radius: 3px;
	max-width: fit-content;
	background-color: var(--default-clr);
	color: rgb(210,210,210);
}
